<template>
  <div class="right">
    <div class="box2">
    <!-- <div class="historyTr">
      <div class="head">${result.hash}</div>
      <div class="body"></div>
      <div class="trDate">2024-04-09</div>
    </div> -->
    </div>
  </div>
</template>

<script>
import Web3 from 'web3';
export default {
  name:'DP-HistoryTrView',
  data() {
    return {
      
    }
  },
  methods: {
    hextoString(str) {
      var out = "", len = str.length/2;
      for(var i = 0; i < len; i++){
          out += String.fromCharCode(parseInt(str.substr(i*2,2),16));  
        }
        // 解决乱码问题
      return this.utf8to16(out)
    },
    utf8to16(str) {
      var out, i, len, c;
      var char2, char3;
      out = "";
      len = str.length;
      i = 0;
      while(i < len) {
        c = str.charCodeAt(i++);
        switch(c >> 4){ 
          case 0: case 1: case 2: case 3: case 4: case 5: case 6: case 7:
                out += str.charAt(i-1);
          break;
          case 12: case 13:
              char2 = str.charCodeAt(i++);
              out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F));
          break;
          case 14:
                char2 = str.charCodeAt(i++);
                char3 = str.charCodeAt(i++);
                out += String.fromCharCode(((c & 0x0F) << 12) |
                ((char2 & 0x3F) << 6) |
                ((char3 & 0x3F) << 0));
            break;
          }
      }
      return out
    }
  },

  mounted() {
    const web3 = new Web3(Web3.givenProvider || "ws://localhost:7545")
    web3.eth.getBlockNumber().then(index => {
      index = Number(index)
      while (index >= 0) {
        web3.eth.getBlock(index).then(block => {
          if (block.transactions === undefined) return

          const transactions = block.transactions
          console.log(transactions)
          transactions.forEach(element => {
            web3.eth.getTransaction(element).then(transaction => {
              if (transaction.data !== '0x') {
                const hexData = transaction.data
                const jsonData = this.hextoString(hexData.substring(2, hexData.length))
                try{
                  const data = JSON.parse(jsonData)
                  document.querySelector('.box2').innerHTML += `<div class="historyTr">
                  <div class="head">${data.sign}</div>
                  <div class="body">
                    type:${data.type}<br>
                    para1:${data.para1}<br>
                    para2:${data.para2}<br>
                  </div>
                  <div class="trDate">${data.ts}</div>
                </div>`
                }catch{
                  console.log(111)
                }
                
              }
            })
          })
        })
        index--
      }
    })
  }
}
</script>

<style>
body {
  background: url("../../images/rightBoxBGI.png") no-repeat;
  background-size: cover;
}

.box2 .historyTr {
  position: relative;
  top: 30px;
  margin-bottom: 30px;
}

.box2 .head {
  position: relative;
  left: 400px;
  top: 10px;
  width: 700px;
  height: 30px;
  padding-top: 3px;
  border: solid 1px blanchedalmond;
  overflow: hidden;
}

.box2 .body {
  position: relative;
  left: 400px;
  top: 10px;
  width: 700px;
  border: solid 1px blanchedalmond;
  white-space: normal;
  word-break: break-all;
  overflow: hidden;
}

.box2 .trDate {
  position: relative;
  left: 1000px;
  top: 10px;
}
</style>